    computed: {
        filtered_routines: function () {
            console.log(this.routines)
            let r = this.routines.map((r) => ({
                category: r.category,
                list: r.list.filter(l => l.toLowerCase().includes(this.search_text))
            })).filter((r) => r.list.length)
            console.log(r)
            return r
        },
        progress(){
